/*---contacts----*/
.contacts{
	min-height: 100%;
	padding-top: rem(147);
	background-color: $text;
	color: #fff;
}
.contacts-container{
	max-width: 1248px;
	margin: 0 auto;
	padding-left: 100px;

	&__inner{
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
	}
}
.contacts-left{
	flex-shrink: 0;
	flex-grow: 1;
}
.ffd-form{
	max-width: 600px;
}
.ffd-form__row{
	margin-bottom: rem(42);
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;

	&--selects{
		padding-left: 20px;
		padding-right: 50px;
	}
}
.ffd-form__label{
	position: relative;	
	display: block;
	width: 257px;
	// flex: 1 1 0;
	border: 2px solid #FFFFFF;

	input[type="text"],
	textarea{
		display: block;
		width: 100%;
		padding: 16px;
		padding-top: 22px;
		background-color: transparent;
		font-family: $din-m;
		font-size: 21px;
		border: none;
		color: #D1D053;
		text-transform: uppercase;

		@include placecolor(#D1D053);
	}
	textarea{
		height: 155px;
	}
	&--big{
		width: 308px;
	}
	&--textarea{
		width: 100%;
		margin-bottom: 20px;
	}
}
.ffd-form__label-mame{
	position: absolute;	
	top: -15px;
	left: 20px;
	display: block;
	padding: 5px;
	background-color: $text;
	max-width: 100%;	
	overflow: hidden;	
	color: #fff;
	font-size: 16px;
	font-family: $din-m;
}
.ffd-form__select{
	width: 230px;
	font-size: 16px;
	font-family: $din-m;
	color: #fff;
}
.ui-selectmenu-button.ui-button{
	width: 100%;
	background-color: transparent;
	border: none;
	color: rgba(#EBEBEB, .35);
	font-size: 21px;
	font-family: $din-mi;
}
.ui-button{
	padding-left: 0;
	padding-right: 0;
	padding-top: 13px;
}
.ui-button:focus{
	outline: none;
}
.ui-button .ui-icon{
	width: 17px;
	height: 15px;
	margin-top: 5px;
	position: relative;


	&:after{
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 150%;
		left: 0;
		top: 0;
		background:  url(../img/icons-svg/chevron-down-solid.svg) 0 0 no-repeat;
		background-size: 100%;
	}


}
.ui-button .ui-icon,
.ui-button:focus .ui-icon,
.ui-button:active .ui-icon,
.ui-button:hover .ui-icon{
	background-image: none;
}
.ffd-form__buttons{
	padding: 5px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: $gold;

	.button{
		background-color: $text;
		color: $gold;
	}
	button{
		margin-right: 26px;
		font-size: 21px;
		font-family: $din-m;
		text-transform: uppercase;
		background-color: transparent;
		border: none;
		cursor: pointer;
		transition: transform .2s ease-out;;

		&:hover{
			transform: scale(1.2);
		}
	}

}
.contacts-info__block{
	position: relative;
	padding-left: 41px;
	margin-bottom: rem(45);
	color: #EBEBEB;
	font-size: 16px;
	line-height: 21px;

	a{
		
	color: #EBEBEB;
	text-decoration: none;
	}


	&:last-child{
		margin-bottom: 0;
	}

	
}
.contacts-info__icon{
	position: absolute;
	left: 0;
	top: 0;
	color: rgba(#EBEBEB, .35);

	i{
		font-size: 18px;
	}
}
.contacts-info__note {
	padding-left: 41px;
	margin-bottom: 20px;
	font-size: 20px;
	line-height: 26px;
	color: rgba(#fff, .55);
}
/*---end contacts----*/